import Page from 'components/Page';
import PropTypes from 'prop-types';
import React from 'react';
import { connect } from 'react-redux';
import { Card, CardBody, CardHeader, Col, Row, Table, Form, Button } from 'reactstrap';

import * as actions from '../store/actions/index';
import LoungeFilter from '../components/Filter/Lounge'
import StatusImage from '../components/TableStatus'
import NotificationSystem from 'react-notification-system';
import { NOTIFICATION_SYSTEM_STYLE } from 'utils/constants';
import RowNoData from '../components/RowNoData';
import {
	MdError
} from 'react-icons/md';
import BasePage from './BasePage';



class TableStatusPage extends BasePage {

	state = {
		filters: {
			lounge_id: null
		},
		searchClicked: false,
		touched: false,
	}

	handleSubmit = () => {
		if (this.state.filters.lounge_id === null) {
			this.notifyMissingField('Seleziona la sala')
			return
		}
		const { searchClicked } = this.state
		this.setState({
			searchClicked: !searchClicked
		})
		this.props.onLoadTables(null, null, this.state.filters, [], null)
	}

	handleFilterChange = (value, name) => {
		const { filters } = this.state
		filters[name] = value
		this.setState({
			filters
		})
	}


	notifyMissingField(message) {
		if (!this.notificationSystem) {
			return;
		}

		this.notificationSystem.addNotification({
			title: <MdError />,
			message: message,
			level: 'error',
		});
	}

	render() {
		let {
			tables
		} = this.props
		let rows = <RowNoData colspan={6} />
		if (this.props.loading === false && this.props.error === false && this.props.tables !== null) {
			rows = tables.map((row, index) => {
				let conto = ''
				switch (row.attributes.status) {
					case 'P':
						conto = 'Preconto'
						break;
					case 'S':
						conto = 'Scontrino'
						break;
					case 'F':
						conto = 'Fattura'
						break;
					default:
						break;
				}
				return <tr key={index}>
					<td><StatusImage status={row.attributes.status} /></td>
					<td>{row.attributes.number}</td>
					<td>{row.attributes.seats}</td>
					<td>{row.attributes.start_time}</td>
					<td>{conto}</td>
					<td>{row.attributes.customer}</td>
				</tr>
			})


		}
		return (
			<Page title="Situazione tavoli" breadcrumbs={[{ name: 'situazione tavoli', active: true }]}>
				<Row>
					<Col>
						<Card className="mb-3">
							<CardHeader>
								Filtri
              </CardHeader>
							<CardBody>
								<Form onSubmit={this.handleSubmit}>
									<Row>
										<LoungeFilter onChange={this.handleFilterChange.bind(this)} />
									</Row>
									<Row>
										<Col>
											<Button
												color="primary"
												disabled={this.props.loading && this.state.searchClicked}
												onClick={this.handleSubmit}>
												Cerca
                                            </Button>
										</Col>
									</Row>
								</Form>
							</CardBody>
						</Card>
						<Card className="mb-3">
							<CardBody>
								<Table responsive>
									<thead>
										<tr>
											<th></th>
											<th>Tavolo</th>
											<th>Coperti</th>
											<th>Apertura</th>
											<th>Conto</th>
											<th>Cliente</th>
										</tr>
									</thead>
									<tbody>
										{rows}
									</tbody>
								</Table>
							</CardBody>
						</Card>
					</Col>
				</Row>
				<NotificationSystem
					dismissible={false}
					ref={notificationSystem =>
						(this.notificationSystem = notificationSystem)
					}
					style={NOTIFICATION_SYSTEM_STYLE}
				/>
			</Page>
		);
	}
}
TableStatusPage.contextTypes = {
	store: PropTypes.object
};

TableStatusPage.propTypes = {
	notifications: PropTypes.array,
	tables: PropTypes.array,
	lounges: PropTypes.array,
	loading: PropTypes.bool,
	error: PropTypes.bool
};

const mapStateToProps = state => {
	return {
		tables: state.tables.tablesState,
		loading: state.tables.loading,
		error: state.tables.error,
		notifications: state.notifications,
	};
};

const mapDispatchToProps = dispatch => {
	return {
		onLoadTables: (page, perPage, filters, include, order) => dispatch(actions.loadTablesStatus(page, perPage, filters, include, order)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(TableStatusPage);
