import React, { Component } from 'react';

import { Col, FormGroup, Label } from 'reactstrap';
import AsyncSelect from 'react-select/async';

import api from '../../api'



export default class Lounge extends Component {
	  state = { options: null,default:null };

		handleChangeLounge = (event) => {
			const value = event ? event.value : null
			this.props.onChange(value, 'lounge_id')
		};


		promiseLoungeOptions = (inputValue) => {
			const url = `lounges${inputValue ? '?filter[text]=' + inputValue : ''}`
			return api.get(url)
				.then(response => {
					let options = response.data.data.map(lounge => ({ value: lounge.id, label: lounge.attributes.name }));

					this.setState({options: options,default:options[0]})
					return options;
				})
				.catch(err => {
					console.log('some error', err);
				});
			};


	  render() {
			return (
				<Col md={6}>
					<FormGroup>
						<Label for="exampleUrl">Sala</Label>
						<AsyncSelect
							isClearable
							cacheOptions
							loadOptions={this.promiseLoungeOptions}
							defaultOptions
							defaultInputValue={this.state.default === null ?  '' : this.state.default.label }
							onChange={(event) => this.handleChangeLounge(event)}
						/>
					</FormGroup>
				</Col>
			);
	  };
};
