import React from 'react';
import PropTypes from 'utils/propTypes';

import engaged from 'assets/img/tables/tavolo_impegnato.png';
import free from 'assets/img/tables/tavolo_libero.png';
import busy from 'assets/img/tables/tavolo_impegnato.png';
import ready from 'assets/img/tables/tavolo_conto.png';
import bill from 'assets/img/tables/tavolo_preconto.png';

const TableStatus = ({
	rounded,
	circle,
	src,
	size,
	tag: Tag,
	className,
	style,
	status,
	...restProps
}) => {
	switch (status) {
		case 'O':
			src = busy
			break;
		case 'P':
			src = ready
			break;
		case 'S':
		case 'F':
			src = bill
			break;
		case 'I':
			src = engaged
			break;
		default:
			src = free
			break;
	}
	return (
		<Tag
			src={src}
			style={{ width: size, height: size, ...style }}
			className={className}
			{...restProps}
		/>
	);
};

TableStatus.propTypes = {
	status: PropTypes.string,
};

TableStatus.defaultProps = {
	tag: 'img',
	rounded: false,
	circle: true,
	size: 40,
	style: {},
};

export default TableStatus;
